.page {
  scroll-behavior: smooth;
  color: #333;
  background: #e4dfd3;
  margin: 0;
  font-family: Poppins, sans-serif;
  font-size: 16px;
  line-height: 140%;
}

.page__body {
  min-width: 320px;
  margin: 0;
  overflow-x: hidden;
}

.page__body--with-menu {
  overflow: hidden;
}

.page__menu {
  pointer-events: none;
  opacity: 0;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  pointer-events: all;
  opacity: 1;
  transform: translateX(0);
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

